<template>
	<div class="newFriends">
		<similarHeader title='新的朋友' @goBack='goBack' :haveMore='false'>
			<h2 class='addFriends'>添加朋友</h2>
		</similarHeader>
		<p class='desc'>你可以绑定手机号，以便找到更多好友。</p>
		<div class="add">
			<img src="~img/images/phone.png" alt="">添加手机联系人
		</div>
	</div>
</template>
<script>
	import  similarHeader from '@/components/similar-header'
	import {mixinTest1} from '@/mixin'
	export default{
		name:'newFriends',
		mixins:[mixinTest1],
		components:{
			similarHeader
		}
	}
</script>
<style scoped>
	.newFriends{
		height: 100vh;
		background: #ededed;
	}
	header.similar{
		border-bottom: 1px solid #ddd;
	}
	header.similar .addFriends{
		position: absolute;
	    right: 0.3rem;
	    top: 50%;
	    transform: translateY(-50%);
	    font-weight: 350;
	    margin:0;
	}
	p.desc{
		font-size: .34rem;
	    color: #888;
	    margin: .3rem 0 .3rem .3rem;
	}
	div.add{
		display: flex;
		align-items: center;
		font-size: .39rem;
	    padding: .28rem .4rem;
	    background: #fff url('~img/images/right.png') no-repeat 92vw center/.3rem;
	}
	.add>img{
		width: .5rem;
	    height: .5rem;
	    margin-right: .4rem;
	}
</style>


